@import url(https://fonts.googleapis.com/css2?family=Sansita+Swashed:wght@400;600;900&display=swap);
.ReviewAccept {
  padding-top: 150px;
}

.padding {
    padding-top: 220px;
    padding-bottom: 15px;
  }
.SponsorPage {
  padding-top: 150px;
}

.jumbotron {
  padding-top: 200px !important;
  background-color: #f0e6db !important;
}
.jumbotronImage {
  max-width: 100%;
  webkit-box-shadow: -31px 10px 27px -26px rgba(148, 143, 148, 1);
  box-shadow: -31px 10px 27px -26px rgba(148, 143, 148, 1);
}

.jumbotron h2 {
  font-family: "Lato", sans-serif;
  font-weight: bold;
  padding-bottom: 50px;
  font-size: 3.6rem;
  line-height: 0.8;
}

body .nav {
  padding-top: 35px;
  padding-left: 85px;
}
body .buttonNav {
  color: white;
  width: 128px;
  font-size: 0.9em;
  font-weight: bold;
  background-color: crimson;
  border-radius: 63px;
  margin-left: 5px;
  text-align: center;
}
.buttonNav:hover {
  color: white;
}

body .captiontext {
  text-align: center;
  font-size: 0.5 rem !important;
}

.col-sm-5 {
  margin: 0 auto;
  padding-top: 28px;
}

.carouselTitle {
  text-align: center;
  padding-bottom: 10px;
}
.carouselContainer {
  background-color: #e5e2da !important;
  width: 70% !important;
  margin: 0 auto !important;
}

.carousel-indicators > li {
  border-radius: 100% !important;
  width: 12px !important;
  height: 12px !important;
}
body .carousel p {
  color: white;
}

body .carousel h2 {
  color: white;
}

@media screen and (max-width: 940px) {
  .carouselContainer {
    width: 80% !important;
  }
}

.featuretteContainer {
  padding: 90px 100px;
  margin: 0 auto;
}

.greasyStatement {
  font-family: "Lato", sans-serif;
  font-size: 4rem;
  letter-spacing: 0.04em;
  font-weight: bold;
  text-align: center;
}

.boldStatement {
  text-align: center;
  font-size: 2rem;
  font-weight: bold;
}

.FeaturetteButton {
  text-align: center;
  color: white;
  width: 140px;
  background-color: crimson;
  border-radius: 63px;
  margin: 0 auto;
}
.FeaturetteButton:hover {
  color: white;
}

@media screen and (max-width: 940px) {
  .featuretteContainer {
    padding: 50px 40px;
    margin: 0 auto;
  }
}

.page-heading {
  text-align: center;
}

.container-FundRequest {
  padding-top: 220px;
  padding-bottom: 15px;
}

.profile-text-description-container {
  padding-top: 220px;
  margin-left: 30%;
}
.profile-text-description {
  padding-top: 30px;
}

.auto-size-textarea {
  width: 100%;
}

.navbar {
  position: fixed !important;
  background-color: white;
  position: fixed;
  z-index: 1;
  top: 0;
  width: 100%;
}
.mr-auto {
  font-family: 'Lato', sans-serif;
}

.navbar-brand h1 {
  font-family: 'Sansita Swashed', cursive;
  font-size: 4.5rem;
}

.icon {
    font-size: 100px;
    display: inline-block;
    transform: scale(1.2);  
}

  
@media screen and (max-width: 940px) {
    div.hideonmobile {
      display: none;
    }

    .navbar-brand h1 {
      font-size: 2.5rem;
    }

    .icon {
      font-size: 50px;
      padding-right: 20px;
      display: inline-block;
      transform: scale(0.8);  
    }
  }
.SponsorRequestDetails {
    padding-top: 150px;
  }
  
.OfferDetailsAccepted {
    padding-top: 150px;
  }
  
.SponsorPageAccepted {
    padding-top: 150px;
  }
  
